<!--
 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-27 16:31:56
 * @LastEditTime: 2019-10-21 18:03:51
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <div class="members">
      <span>
        <p class="memb">
          {{$t('quote_member')}}
          <span>{{indexList+1}}</span>
        </p>
        <p>{{$t('relationship')}}</p>
        <p>{{$t('Gender')}}</p>
        <p>{{$t('account_info_lastname2')}}</p>
        <p>{{$t('account_info_firstname2')}}</p>
        <p>{{$t('account_info_birthday')}}</p>
        <p>{{isHkid}}</p>
      </span>
      <span class="members-right">
        <p @click="toMemberList" class="edit">{{$t('firm_order_change')}}</p>
        <p>{{userData.title}}</p>
        <p>{{userData.gender}}</p>
        <p>{{userData.surname}}</p>
        <p>{{userData.givename}}</p>
        <p>{{userData.datebirth}}</p>
        <!-- HKID或者passport -->
        <p>{{HkidpassportData}}</p>
      </span>
    </div>
  </div>
</template>
<script>
export default {
  name: "order",
  props: {
    userData: {
      require: true,
      type: Object
    },
    indexList: {
      require: true,
      type: Number
    }
  },
  data() {
    return {
      //定义一个属性来显示KHID或者passport
      isHkid: this.$t('account_info_hkid'),
      //用来保存HKID或者passport
      HkidpassportData: ""
    };
  },
  methods: {
    HkidPassport() {
      console.log(this.userData);
      if (
        this.userData.HKID1.trim() == "" &&
        this.userData.HKID2.trim() == ""
      ) {
        this.isHkid = this.$t('info_passport');
        this.HkidpassportData = this.userData.HKID_passport;
      } else if (
        this.userData.HKID1.trim() != "" &&
        this.userData.HKID2.trim() == ""
      ) {
        this.HkidpassportData = this.userData.HKID1 + this.userData.HKID2;
      } else {
        this.HkidpassportData =
          this.userData.HKID1 + "(" + this.userData.HKID2 + ")";
      }
    },
    //点击编辑跳转到成员信息列表
    toMemberList() {
      if (this.userData.title == this.$t('policyholder')) {
        this.$router.push("/Policyholder");
      } else {
        this.$router.push("/MemberData");
      }
    }
  },
  created() {
    console.log(this.userData);
    this.HkidPassport();
  }
};
</script>
<style lang="less" scoped>
@import "~style/mixin.less";
.members {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  span {
    line-height: 40px;
    .memb {
      color: #9b9b9b;
      margin-bottom: 20px;
    }
    .edit {
      margin-bottom: 20px;
      text-align: right;
      color: @blue;
    }
  }
  .members-right {
    text-align: right;
  }
}
</style>


