 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-29 10:30:48
 * @LastEditTime: 2019-10-12 16:42:43
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <div class="headTitle">
      <i @click="toPrePage" class="cubeic-arrow"></i>
      <p>{{$t('firm_order_title')}}</p>
    </div>
    <div class="plan-item">
      <p class="plan">{{$t('firm_order_plan_name')}}</p>
      <p class="travelead">{{priceSymbol}}{{product_name}}</p>
    </div>

    <div class="pre-item">
      <p class="premium">{{$t('firm_order_premium')}}</p>
      <p class="price">HK${{price}}</p>
    </div>

    <div class="date">
      <span>
        <p class="date-title">{{$t('firm_order_star_date')}}</p>
        <p class="date-date">{{departureData}}</p>
      </span>
      <span>
        <p class="date-title">{{$t('firm_order_end_date')}}</p>
        <p class="date-date">{{returnData}}</p>
      </span>
      <span @click="back" class="edit">{{$t('firm_order_change')}}</span>
    </div>
    <!-- 保单人信息 -->
    <WOrder v-if="isShow" :indexList="0" :userData="this.$store.state.protectorInfo"></WOrder>
    <!-- 调用组件 -->
    <WOrder
      v-for="(item,index) in userInfo"
      :key="index"
      :indexList="index | newIndex(isShow)"
      :userData="item"
    ></WOrder>

    <div class="button-bar">
      <router-link to="/Payment">
        <button class="proceed">{{$t('confirm')}}</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import WOrder from "components/WOrder.vue";
export default {
  components: {
    WOrder
  },
  data() {
    return {
      isShow: false,
      //定义一个变量来显示DepartureData
      departureData: "",
      //定义一个变量来显示returnData
      returnData: "",
      //价格
      price: ""
    };
  },
  created() {
    console.log(this.userInfo);
    
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
    //如果vuex中有保单人数据
    if (this.$store.state.protectorInfo) {
      this.isShow = true;
    } else {
      this.isShow = false;
    }
    //页面加载获取vuex中的时间
    this.getData();
    console.log(this.returnData);
    console.log(this.$store.state.travelInfo.date.returnDate);
  },
  computed: {
    ...mapState([
      "travelInfo",
      "product_name",
      "product_id",
      "language",
      "plan",
      "userId",
      'userInfo',
      'priceSymbol',
      'protectorInfo'
    ]),
    // getDepartureData() {
    //   return this.$store.state.travelInfo.date.departureDate;
    // },
    // getReturnData() {
    //   return this.$store.state.travelInfo.date.returnDate;
    // },
  },
  //过滤器:判断保单人是否存在,存在的话序号加2
  filters: {
    newIndex(val, val2) {
      if (val2 == true) {
        return val + 1;
      } else {
        return val;
      }
    }
  },
  methods: {
    ...mapMutations(["setCommonKey"]),
    back() {
      this.$router.push("/TravelInfo");
    },
    //创建一个方法来从vuex中获取时间显示时间
    getData() {
      this.departureData = this.$store.state.travelInfo.date.departureDate;
      this.returnData = this.$store.state.travelInfo.date.returnDate;
    },
    //跳转到上一页
    toPrePage() {
      console.log(this.protectorInfo)
      if(this.protectorInfo){
        this.$router.push('/Policyholder')
      }else{
        this.$router.push('/MemberData')
      }
    }
  },
  mounted() {
    let others_insured_list = [];
    const { count } = this.travelInfo;
    if (count.myself) {
      others_insured_list.push({ relation: "2001" });
    }
    if (count.spouse) {
      others_insured_list.push({ relation: "2002" });
    }
    if (count.child) {
      others_insured_list.push({ relation: "2003" });
    }
    if (count.friends) {
      others_insured_list.push({ relation: "2004" });
    }

    var priceParams = {
      product_id: this.plan.product_id,
      language: this.language,
      start_date: this.travelInfo.date.departureDate.replace(/\//g, "-"),
      end_date: this.travelInfo.date.returnDate.replace(/\//g, "-"),
      others_insured_list
    };
    // 日计划  月计划   年计划
    console.log(this.travelInfo.date.departureDate);
    console.log(this.travelInfo.date.returnDate);

    //调用接口获取价格
    this.$http
      .post("/wesupromo/web/product/getstarrquote", priceParams)
      .then(res => {
        console.log(res);
        this.price = res.data.premium;
      });

    //獲取公鑰
    this.$http
      .post("/wesupromo/web/getpublicKey", {
        id: this.userId
      })
      .then(res => {
        console.log(res);
        if(res.status == 100){
           this.setCommonKey(res.data.public_key);
        }
       
      });
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: @background;
  position: relative;
  padding-bottom: 280px;
  padding-top: 120px;
  .imgs {
    width: 100%;
  }
  .img {
    width: 100%;
    height: 100%;
  }
  .plan-item {
    line-height: 40px;
    .plan {
      color: #9b9b9b;
    }
    .travelead {
      font-family: PingFangHK-Regular;
      font-size: 36px;
    }
  }
  .pre-item {
    line-height: 40px;
    margin-top: 40px;
    .premium {
      color: #9b9b9b;
      font-family: PingFangHK-Regular;
    }
    .price {
      color: @blue;
      font-weight: bold;
      font-size: 42px;
    }
  }
  .date {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    line-height: 45px;
    span {
      .date-title {
        color: #9b9b9b;
        font-family: PingFangHK-Regular;
      }
      .date-date {
        font-size: 30px;
      }
    }
    .edit {
      color: @blue;
    }
  }

  .button-bar {
    margin: 0 auto;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 200px;
    border-top: 3px solid rgba(0, 0, 0, 0.25);
    background-color: rgba(250, 250, 250);
  }
  .proceed {
    width: 350px;
    height: 100px;
    line-height: 100px;
    border: none;
    margin-top: 50px;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
  .headTitle {
    position: fixed;
    height: 100px;
    top: 0;
    width: 100%;
    left: 0;
    z-index: 99;
    background-color: rgba(250, 250, 250);
    border-bottom: 2px solid rgba(0, 0, 0, 0.25);
    .cubeic-arrow {
      color: rgb(0, 122, 255);
      display: inline-block;
      transform: rotateZ(180deg);
      font-size: 32px;
      font-weight: bold;
      position: absolute;
      left: 0px;
      line-height: 100px;
      width: 50px;
      text-align: center;
      height: 100%;
    }
    p {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      font-size: 30px;
      font-weight: 600;
      margin-top: 40px;
    }
  }
}
</style>


